import { Component } from '@angular/core';
import { NavController, PopoverController } from 'ionic-angular';
//import { Storage } from '@ionic/storage';
import { NotificationsPage } from '../notifications/notifications';
import { SearchGamePage } from '../search-game/search-game';
import { GamesPage } from '../games/games';
import { SettingsPage } from '../settings/settings';

import { Http} from '@angular/http';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { SocialSharing } from '@ionic-native/social-sharing';
import {Screenshot } from '@ionic-native/screenshot';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public search = {
    name: "BRASIL vs SUIÇA", // DEFININDO O 1 CONFRONTO QUE APARECE NA TELA
    date: new Date().toISOString()
  }
  public tabelas =[];
  public message = null;
  public subject = null;
  public file = '';
  public link ='';

  

  // tenho que add private storage: Storage no construtor mas fica dando erro ainda
  constructor(private screenshot:Screenshot,private socialSharing: SocialSharing,public http:Http,public navCtrl: NavController, public popoverCtrl: PopoverController,public iab:InAppBrowser) {
    this.modeloGet();
  }
  modeloGet(){
    this.tabelas = [];
    let parametros = 'futebol';
    let headers = '';
    let url = `https://content.guardianapis.com/search?q=${parametros}&api-key=test`;

    this.http.get(url, headers).subscribe(
      sucesso => { 
        let news = sucesso.json().response.results;
        console.log(sucesso.json());
        for( let i=0;i < news.length ; i++){
          this.tabelas.push({
            titulo: news[i].webTitle,
            categoria: news[i].sectionName,
            url: news[i].webUrl
          });
        }
      },
      erro => { console.error(erro); }
    );
  }

  abrirPagina(object){
    let url  = object.url;
    const browser = this.iab.create(url);
    browser.show();
  }

  socialS(object){
    let msg = object.webTitle;
    let url = object.url;
    this.socialSharing.share(msg,this.subject,this.file,url).then(()=>{
    } ).catch(()=>{

    });
  }

  salvarFoto(){
    this.screenshot.save('jpg',80,'myscreenshot.jpg');

  }
   // ESCOLHER CONFRONTO
   chooseBet(from) {
    this.navCtrl.push(SearchGamePage, from);
  }

  // FUNÇÃO PARA CRIAR NOVO BOLÃO (ajeita aqui) 
  doCreate(){
  }

  // FUNÇÃO PARA LISTAR BOLÕES ATIVOS (ajeita aqui)
  doSearch(){
    this.navCtrl.push(GamesPage);
  }

  // CONTROLE DE NOTIFICAÇÕES
presentNotifications(myEvent) {
  console.log(myEvent);
  let popover = this.popoverCtrl.create(NotificationsPage);
  popover.present({
    ev: myEvent
  });
}

goToAccount(){
  this.navCtrl.push(SettingsPage);
}


}



